.jptb-top-panel {
  box-sizing: border-box;
}

.jptb-top-panel * {
  box-sizing: inherit;
}

.jptb-top-panel *:before, .jptb-top-panel *:after {
  box-sizing: inherit;
}

@font-face {
  font-family: DinPro;
  src: url(../fonts/DINPro.otf);
}

.jptb-top-panel-wrapper {
  position: relative;
  height: 34px;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.jptb-top-panel {
  display: flex;
  justify-content: space-between;
  font-family: DinPro, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  height: 34px;
  line-height: 34px;
  padding: 0 13px;
  background-color: #000;
  color: #fbea5e;
  font-size: 23.8px;
  text-shadow: 1px 1px 1px rgba(251, 234, 94, 0.6);
  text-transform: uppercase;
  white-space: nowrap;
  overflow: hidden;
}

.jptb-top-panel .jptb-left, .jptb-top-panel .jptb-right {
  flex: 0;
}

.jptb-top-panel .jptb-right {
  text-align: right;
}

.jptb-top-panel .jptb-center {
  flex: 1;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  transition: transform 1200ms;
}

.jptb-top-panel .jptb-center .jptb-jackpot-item {
  display: flex;
  padding: 0 8px;
  margin-bottom: 6px;
}

.jptb-top-panel .jptb-center .jptb-jackpot-item .jptb-jackpot-name {
  margin-right: 11px;
}

.jptb-top-panel .jptb-center .jptb-jackpot-item .jptb-jackpot-counter {
  font-size: 19px;
  position: relative;
}

.jptb-top-panel .jptb-center .jptb-jackpot-item .jptb-jackpot-counter .jptb-jackpot-counter-wrapper {
  display: flex;
  overflow: hidden;
  background-image: linear-gradient(top, rgba(200, 200, 200, 0.2) 0%, rgba(200, 200, 200, 0.2) 40%, black 45%, black 55%, black 100%);
  background-color: #222222;
  border-radius: 3px;
}

.jptb-top-panel .jptb-center .jptb-jackpot-item .jptb-jackpot-counter .jptb-jackpot-counter-wrapper .jptb-jackpot-counter-cell {
  width: 22.1px;
  height: 34px;
  position: relative;
  transform: rotateX(-12deg);
  transform-style: preserve-3d;
}

.jptb-top-panel .jptb-center .jptb-jackpot-item .jptb-jackpot-counter .jptb-jackpot-counter-wrapper .jptb-jackpot-counter-cell + .jptb-top-panel .jptb-center .jptb-jackpot-item .jptb-jackpot-counter .jptb-jackpot-counter-wrapper .jptb-jackpot-counter-cell {
  box-shadow: inset 0.09em 0 0.1em 0px rgba(255, 255, 255, 0.2);
}

.jptb-top-panel .jptb-center .jptb-jackpot-item .jptb-jackpot-counter .jptb-jackpot-counter-wrapper .jptb-jackpot-counter-cell > div {
  margin: 0 auto;
  height: 34px;
  width: 20px;
  transform-style: preserve-3d;
  will-change: transform;
  transition-property: transform;
  transition-duration: 0ms;
  transition-timing-function: cubic-bezier(0.7, 0.16, 0.3, 0.84);
  transition-delay: 500ms;
}

.jptb-top-panel .jptb-center .jptb-jackpot-item .jptb-jackpot-counter .jptb-jackpot-counter-wrapper .jptb-jackpot-counter-cell .jptb-plane {
  position: absolute;
  height: 34px;
  width: 20px;
  text-align: center;
  line-height: 1;
  color: #fbea5e;
  backface-visibility: hidden;
}

.jptb-jackpot-counter-cell:nth-child(6):before {
  content: "\002E";
  display: block;
  position: absolute;
  left: -3px;
}

.jptb-jackpot-counter .jptb-figure0 {
  transform: rotate3d(1, 0, 0, 0deg) translate3d(0, 0, 31px);
}

.jptb-jackpot-counter .jptb-figure1 {
  transform: rotate3d(1, 0, 0, -36deg) translate3d(0, 0, 31px);
}

.jptb-jackpot-counter .jptb-figure2 {
  transform: rotate3d(1, 0, 0, -72deg) translate3d(0, 0, 31px);
}

.jptb-jackpot-counter .jptb-figure3 {
  transform: rotate3d(1, 0, 0, -108deg) translate3d(0, 0, 31px);
}

.jptb-jackpot-counter .jptb-figure4 {
  transform: rotate3d(1, 0, 0, -144deg) translate3d(0, 0, 31px);
}

.jptb-jackpot-counter .jptb-figure5 {
  transform: rotate3d(1, 0, 0, -180deg) translate3d(0, 0, 31px);
}

.jptb-jackpot-counter .jptb-figure6 {
  transform: rotate3d(1, 0, 0, -216deg) translate3d(0, 0, 31px);
}

.jptb-jackpot-counter .jptb-figure7 {
  transform: rotate3d(1, 0, 0, -252deg) translate3d(0, 0, 31px);
}

.jptb-jackpot-counter .jptb-figure8 {
  transform: rotate3d(1, 0, 0, -288deg) translate3d(0, 0, 31px);
}

.jptb-jackpot-counter .jptb-figure9 {
  transform: rotate3d(1, 0, 0, -324deg) translate3d(0, 0, 31px);
}

/* win animation */
.jptb-win {
  order: -1 !important;
  flex-basis: 100%;
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
  position: relative;
  z-index: 100;
}

.jptb-win:after {
  z-index: 10;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 200%;
  transform: translateX(0%);
  background: linear-gradient(0% -15%, #655bef 0%, #fa28bf 20%, #ff4665 40%, #fbde4e 60%, #00fbea 80%, #555def 100%);
  background: -moz-linear-gradient(0% -15%, #655bef 0%, #fa28bf 20%, #ff4665 40%, #fbde4e 60%, #00fbea 80%, #555def 100%);
  background: -webkit-gradient(linear, 0% -15%, 100% 100%, color-stop(0, #655bef), color-stop(0.2, #fa28bf), color-stop(0.4, #ff4665), color-stop(0.6, #fbde4e), color-stop(0.8, #00fbea), color-stop(1, #555def));
  -o-animation: anime 17.5s infinite;
  -ms-animation: anime 17.5s infinite;
  -moz-animation: anime 17.5s infinite;
  -webkit-animation: anime 16s infinite;
  z-index: -1;
}

@keyframes anime {
  0% {
    transform: translateX(0%);
  }
  50% {
    transform: translateX(-150%);
  }
  100% {
    transform: translateX(0%);
  }
}
